body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --mainColor: red;
  --secendColor: black;
  --thirdColor: #f3f3f3;
  --fourthColor: #232528;
  --fifthColor: #ffa400;

}

body {
  font-family: 'Lato', sans-serif !important;
  background: #f3f3f3 !important;
  background: var(--thirdColor) !important;
  color: #232528 !important;
  color: var(--fourthColor) !important;
}

.text-title {
  font-family: 'Lato', sans-serif, bold;
  letter-spacing: 0.3rem;
  text-transform: uppercase;


}

.text-blue {
  color: red;
  color: var(--mainColor);

}

.text-bright {
  color: black;
  color: var(--secendColor)
}

.btn-black {
  background: transparent;
  text-transform: capitalize;
  font-size: 0.8rem !important;
  color: #232528;
  color: var(--fourthColor);
  border-radius: 0 !important;
  border: 0.1rem solid #232528 !important;
  border: 0.1rem solid var(--fourthColor) !important;
}
.btn-black:hover {
  background: #232528 !important;
  background: var(--fourthColor) !important;
  color: #f3f3f3 !important;
  color: var(--thirdColor) !important;
}
.cart-icon {
  cursor: pointer;
  font-size: 1.2rem;
  color: #ffa400;
  color: var(--fifthColor);
}

